<template>
  <div class="post">
    <div class="post-title-section">
      <router-link :to="`/profile/${post.writer.id}`" :userId="post.writer.id">
        <img :src="post.writer.profileImageUrl" style="width: 30px; height: 30px; border-radius: 50%;"/>
        <b style="font-size: 15pt;">{{ post.writer.nickname }}</b><br/>
      </router-link>
      <span style="color:#777777; font-size: 10pt;">{{ post.title }}</span>
    </div>
    <div class="post-content-section">
      <img v-if="post.postImageUrl !== null" :alt="post.title" :src="post.postImageUrl" style="width: 300px;"/>
      <p>{{ post.content }}</p>

    </div>
    <div class="post-actions">
      <span v-if="!post.likeState" @click="likePost" style="margin: auto;     display: inline-block;">
          <i class="far fa-heart" style="color: red;"></i>
          {{ post.likeCount }}
      </span>
      <span v-if="post.likeState" @click="unLikePost" style="margin: auto;     display: inline-block;">
          <i class="fas fa-heart" style="color: red"></i>
          {{ post.likeCount }}
      </span>
      <span v-if="isOwner===true" style="padding-left: 40px">
        <button class="edit-btn" @click="openEditModal">Edit</button>
        <button class="delete-btn" @click="deletePost">Delete</button>
        <button class="share-btn">Share</button>
      </span>
    </div>
    <CommentBox :comments="post.comment" :postId="post.id"
                @comment-posted="handleCommentPosted"
                @comment-deleted="handleCommentDeleted"
    />
  </div>

  <div>


  </div>
</template>


<script>
import axios from "axios";
import CommentBox from "./CommentBox.vue";
import {apiURL} from "@/services/apiService";

export default {
  name: "PostBox",
  props: {
    post: Object,
  },
  data() {
    return {
      isOwner: false,
    };
  },
  computed: {
    computeIsOwner() {
      return this.post.writer.id === this.$store.state.user.id;
    },
  },
  created() {
    this.isOwner = this.computeIsOwner;
  },
  components: {
    CommentBox,
  },
  methods: {
    handleCommentPosted(comment, writer) {
      this.$emit('comment-posted', comment, writer);
    },
    handleCommentDeleted(comment) {
      this.$emit('comment-deleted', comment, this.post.id);
    },
    async likePost() {
      await axios.post(apiURL + '/likes/' + this.post.id, null, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      });
      this.$emit('like-post', this.post.id);
    },
    async unLikePost() {
      try {
        await axios.delete(apiURL + '/likes/' + this.post.id, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });
        this.$emit('unlike-post', this.post.id);
      } catch (error) {
        console.error(error);
      }
    },
    async deletePost() {
      if (!confirm('정말 삭제하시겠습니까?')) {
        return;
      }

      try {
        await axios.delete(apiURL + '/post/delete/' + this.post.id, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });
        this.$emit('delete-post', this.post.id);
      } catch (error) {
        console.error(error);
      }
    },
    openEditModal(){

    },
    async editPost() {

      this.$emit('edit-post', this.post.id);
    },
  },
};
</script>

<style scoped>
.post {
  background-color: #f8f8f8;
  border-radius: 4px;
  padding: 24px;
  width: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 24px;
  border: 1px solid #ccc;
}

.post-actions {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 8px;
}

a {
  text-decoration: none;
}

a > b {
  color: #2c3e50; /* Changes link color to black */
}

.edit-btn,
.delete-btn {
  background-color: transparent;
  color: #42b983;
  border: none;
  cursor: pointer;
  font-weight: bold;
  margin-left: 8px;
}

.edit-btn:hover,
.delete-btn:hover {
  color: #2c3e50;
}

h2 {
  margin-top: 0;
}

button {
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  background-color: transparent;
}

</style>
