<template>
  <div style="height: 50px;">
    <input type="file" id="file" @change="uploadResource" style="display: none;">
    <label for="file" style="color: blue;cursor: pointer">업로드</label>
    <input type="button" id="remove" @click="removeResource" style="display: none;">
    <label for="remove" style="color: red;cursor: pointer">삭제</label>
  </div>
  <div>
     <img id="img"/>
  </div>
</template>


<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "FileView",
  methods: {
    uploadResource() {
      const file = document.getElementById("file");
      const formData = new FormData();
      formData.append("file", file.files[0]);
      console.log(formData);

      axios.post(apiURL + "/s3/resource", formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      }).then((response) => {
        let result = response.data;
        console.log(result);
        document.getElementById("img").setAttribute("src", result.path);
        document.getElementById("remove").setAttribute("key", result.key)
      }).catch(error => console.log(`error => ${error}`));
    },

    removeResource() {
      const key = document.getElementById("remove").getAttribute("key");
      if (!key) {
        return;
      }
      const formData = new FormData();
      formData.append("key", key);

      fetch("/s3/resource", {
        method: "DELETE"
        , body: formData
      }).then(result => {
        if (result.ok && result.status === 200) {
          alert("해당 이미지가 삭제되었습니다.");
          document.getElementById("img").removeAttribute("src");
        }
      }).catch(error => console.log(`error => ${error}`));
    }

  },
}
</script>


