<template>
  <div>
    <img @click="postDetail" :src="`${post.postImageUrl}`" alt="" style="width: 100px; height: 100px;">
  </div>

  <div v-if="showShareModal===1" class="goal-view-modal" @click.stop @keydown.stop>
    <div class="goal-view-modal-content">
      <span class="close" @click="closeShareModal">&times;</span>
      <p>달성 계획: {{ post.title }}</p>
      <img id="profile-image" @click="openFileInput" style="width: 200px; "
           alt=""
           :src="`${post.postImageUrl}`">
      <br/>
      {{post.content}}
      <input type="file" id="post-image-input" @change="handleFileInputChange" style="display: none;">
      <br>
      <div class="comment-box">
        <div class="comment" v-for="c in post.comment" :key="c.id">
          <p><b>{{ c.writer.nickname }}</b> {{ c.comment }}</p>
          <button @click="deleteComment(c)" class="comment-delete-button">x</button>
        </div>
        <form class="comment-form">
          <input ref="written-comment-input" type="text" placeholder="댓글을 작성해 보세요."/>
          <button class="comment-submit" type="button" @click="submitComment">Submit</button>
        </form>
      </div>
    </div>

  </div>
</template>

<script>


import {apiURL} from "@/services/apiService";
import axios from "axios";

export default {
  name: "UserPost",
  props: {
    post: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      showShareModal: 0,
      shareGoal: {
        title: '',
        content: '',
        postImageUrl: '',
      },
      previewImage: '',
      writtenComment: {},
      writer:{},
    };
  },
  methods: {
    postDetail() {
      this.showShareModal = 1;
    },
    closeShareModal() {
      this.showShareModal = 0;
    },
    async deleteComment(comment) {
      await axios.delete(apiURL + '/comment/delete/' + comment.id, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      });
      this.$emit('comment-deleted', comment, this.post.id);
    },
    async submitComment() {
      let writtenContent = this.$refs["written-comment-input"].value;
      this.writtenComment.comment = writtenContent;
      this.writtenComment.postId = this.post.id;

      await axios.post(apiURL + '/comment/write', this.writtenComment, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      });
      this.writer.nickname = this.$store.state.user.nickname;
      console.log(this.writer);
      this.$emit('comment-posted', this.writtenComment, this.writer);
      writtenContent = ''; // 댓글 작성 후 입력 필드 초기화
    },
  },
}
</script>


<style scoped>
/* 모달 스타일 */
.goal-view-modal {
  display: flex;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  justify-content: center;
  align-items: center;
}
.goal-view-modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  width: 80%;
  max-width: 600px;
  border: 1px solid #42b983;
  border-radius: 10px;
}

.comment-submit{
  width: 100px;
  background-color: #42b983;
  border: none;
  color: #fff;
  padding: 8px 12px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}
/* 닫기 버튼 스타일 */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}


.comment-box {
  margin-top: 24px;
  border-top: 1px solid #ccc;
  padding-top: 16px;
}

.comment {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
}

.comment-delete-button {
  border: none;
  background-color: transparent;
  width: 5px;
  height: 5px;
}

.comment-form {
  display: flex;
}

input[type="text"] {
  width: 100px;
  flex-grow: 1;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  margin-right: 8px;
}

.comment-submit {
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
  width: 80px;
}

.comment-submit:hover {
  width: 80px;
  background-color: #2c3e50;
}
</style>