<template>
  <div class="profile-container">
    <div class="user-info">
      <img id="profile-image" @click="openFileInput" class="profile-image" :src="user.profileImageUrl" alt="">
      <input type="file" id="profile-image-input" @change="handleFileInputChange" style="display: none;">
      <b class="profile-header">{{ user.nickname }}</b>
      <span> 팔로워 <br/></span>
    </div>

    <div v-if="isOwner" class="login-user-section">
      <router-link :to="`/profile/${user.id}/edit`">
        <button>프로필 편집</button>
      </router-link>
    </div>
    <div v-if="!isOwner" class="explore-user-section">
      <button v-if="!subscribeState" @click="followUser">팔로우</button>
      <button v-if="subscribeState" @click="unFollowUser">팔로잉</button>
      <!--      <button>메시지</button>-->
    </div>
  </div>
  <div class="user-post-area">
    <div v-for="post in posts" :key="post.id">
      <UserPost :post="post"
                @comment-deleted="handleCommentDeleted"
                @comment-posted="handleCommentCreated"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";
import UserPost from "@/components/user/profile/UserPost.vue";

export default {
  name: 'UserProfileView',
  components: {UserPost},
  data() {
    return {
      user: {},
      posts: [],
      profileId: '',
      userId: '',
      isOwner: false,
      subscribeState: false,
    };
  },

  created() {
    console.log(this.userId);
    this.fetchUserData();
    this.fetchPostData();
  },
  watch: {
    '$route.params': {
      immediate: true,
      handler(params) {
        if (params.userId) {
          this.userId = params.userId;
        }
      },
    },
  },
  methods: {
    async fetchUserData() {
      await axios.get(apiURL + '/user/profile/' + this.userId, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        this.user = response.data;
        if (this.user.username === this.$store.state.user.username) {
          this.isOwner = true;
        }
      }).catch((error) => {
        console.log(error);
      });
    },
    async fetchPostData() {
      await axios.get(apiURL + '/post/profile/' + this.userId, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        this.posts = response.data.content;
        console.log(this.posts);
        if (this.user.username === this.$store.state.user.username) {
          this.isOwner = true;
        }
      }).catch((error) => {
        console.log(error);
      });
    },
    async followUser() {
      try {
        await axios.post(apiURL + `/subscribe/${this.user.id}`, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('user-token')}`,
          },
        });
        this.subscribeState = true;
      } catch (error) {
        console.log(error);
      }
    },
    async unFollowUser() {
      try {
        await axios.delete(apiURL + `/subscribe/${this.user.id}`, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('user-token')}`,
          },
        });
        this.subscribeState = false;
      } catch (error) {
        console.log(error);
      }
    },
    async uploadResource() {
      const file = document.getElementById("profile-image-input");
      const formData = new FormData();
      formData.append("profileImage", file.files[0]);
      formData.append("userId", this.user.id);

      console.log(this.user.id);
      await axios.put(apiURL + "/user/profile/image", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          "Authorization": `Bearer ${localStorage.getItem('user-token')}`,
        }
      }).then((response) => {
        let result = response.data;
        document.getElementById("profile-image").setAttribute("src", result.path);
      }).catch(error => console.log(`error => ${error}`));
    },
    openFileInput() {
      const file = document.getElementById('profile-image-input');
      file.click();
    },
    handleFileInputChange(event) {
      const selectedFile = event.target.files[0];
      if (selectedFile) {
        this.uploadResource();
        console.log('선택된 파일:', selectedFile);
      }
    },
    handleCommentDeleted(comment, postId) {
      const post = this.posts.find((post) => post.id === postId);
      post.comment = post.comment.filter((c) => c.id !== comment.id);
    },
    handleCommentCreated(comment, writer) {
      console.log(comment);
      console.log(writer);
      comment.writer = writer;
      const post = this.posts.find((post) => post.id === comment.postId);
      post.comment.push(comment);
    },

  },
};
</script>

<style scoped>
.profile-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.profile-header {
  color: #333;
  font-size: 24px;
  margin-bottom: 24px;
}

.profile-image {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-bottom: 24px;
}

.login-user-section button,
.explore-user-section button {
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
  font-weight: bold;
  cursor: pointer;
  width: 90px;
}

.user-post-area {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3개의 열로 구성 */
  gap: 10px; /* 이미지 간격 설정 */
}

.container {
  flex-direction: row;
  justify-content: start;
  align-items: center;
  width: 100%;
}

.user-post-area img {
  width: 100%; /* 이미지 너비 조정 */
  height: auto; /* 이미지 높이 자동 조정 */
}
</style>

